@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif
}

body {
  background:linear-gradient(90deg, rgba(110, 94, 254, 0.3) 50% , rgba(73, 63, 252, 0.3) );
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 342;
}
.navbar {
    background: linear-gradient(90deg, rgb(110, 94, 254) 0%, rgba(73, 63, 252, 1) 100%);
    height: 80px;
    margin: auto;
    display: flex;
    font-size: 1.2rem;
    animation: left-to-right 5s ease-in infinite alternate;
  }

@keyframes left-to-right {
   0% {
    background: rgb(110, 94, 254)
   }

   100% {
    background: rgba(73, 63, 252, 1);
   }
}
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
  }
  
  .nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links:hover, .nav-links:focus-within {
    background-color: rgba(242, 242, 242, 0.5);
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  

  .shadow {
      position:relative;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  }

  .dropdown-menu {
      display: none;
      width: 200px;
      position: absolute;
      top: 80px;
      list-style: none;
      text-align: start;
  }

  .dropdown-menu li{
      display: block;
      color: #1888ff;
      background: white ;

  }
  
  .links {
      display: block;
      color: #1888ff;
      padding: 16px;
  }

  .links:hover {
      text-decoration: underline;
      transition: all 2s ease-out;
  }
  
  .nav-item:hover .Entry {
      display: block;
  }

  .nav-item:hover .Reports {
      display: block;
    }

    .nav-item:hover .New {
        display: block;
    }

    .nav-item:focus-within .Entry {
        display: block;
    }

    .nav-item:focus-within .Reports {
      display: block;
  }

  .nav-item:focus-within .New{
    display: block;
}

.button {
    margin-top: 10px;
    padding: 10px;
    color: #5f21fc;
    background-color: white ;
    border: 1px solid #5f21fc;
    border-radius: 5%;
}

.button:hover {
    background-color: #d8c9ff ;
    cursor: pointer;
}

.entry_content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;    
}

.form-box {
    padding: 50px;
    border-radius: 5px;
    background-color: white;
    margin-top: 10px;
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.heading {
    display: block;
    text-align: center;
}
.name_credit {
    display: flex;
    border: solid 1px black;
    justify-content: space-around;
    margin-bottom: 5px;
}


.section-header {
    color: #5f21fc;
    font-size: 20px;
    font-weight: bold;
}

.date {
    display: inline;
}

h3, h4 {
    display: inline-block;
    margin-right: 5px;
}



.payment_information {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.fieldset {
    border: solid 2px #C9C6FF;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap: 7%;
    gap: 7%;
    margin: 10px;
    padding: 20px;
}

.bank {
    border: solid 2px #C9C6FF;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0px 35px 5px 35px;
}

.dark-purple { 
    color:#3701c2;
}

.bank_info {
    border: solid 1px #827cfc;
    padding: 5px;
    border-radius: 10px;
}

.selected_payment{
    margin: 20px;

    display: flex;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
    flex-wrap: wrap;
}


.hide {
    display: none
}

.show {
    display: block;
}

.credit {
    padding: 20px;
    border: solid 2px #C9C6FF;
    border-radius: 10px;
}

.additional {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
    justify-content: center;
}

.deduction_percent {
    border-radius: 100% 0% 0% 100%;
}

.deduction_amount {
    border-radius: 0% 100% 100% 0%;
}

.deduct {
    display: flex;
    align-items: center;
    justify-content: center;
}

.selected {
    background-color: #5f21fc;
    color: white;
}
