@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif
}

body {
  background:linear-gradient(90deg, rgba(110, 94, 254, 0.3) 50% , rgba(73, 63, 252, 0.3) );
}

.sticky {
  position: sticky;
  top: 0;
  z-index: 342;
}
.navbar {
    background: linear-gradient(90deg, rgb(110, 94, 254) 0%, rgba(73, 63, 252, 1) 100%);
    height: 80px;
    margin: auto;
    display: flex;
    font-size: 1.2rem;
    animation: left-to-right 5s ease-in infinite alternate;
  }

@keyframes left-to-right {
   0% {
    background: rgb(110, 94, 254)
   }

   100% {
    background: rgba(73, 63, 252, 1);
   }
}
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
  }
  
  .nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links:hover, .nav-links:focus-within {
    background-color: rgba(242, 242, 242, 0.5);
    border-radius: 4px;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  

  .shadow {
      position:relative;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  }

  .dropdown-menu {
      display: none;
      width: 200px;
      position: absolute;
      top: 80px;
      list-style: none;
      text-align: start;
  }

  .dropdown-menu li{
      display: block;
      color: #1888ff;
      background: white ;

  }
  
  .links {
      display: block;
      color: #1888ff;
      padding: 16px;
  }

  .links:hover {
      text-decoration: underline;
      transition: all 2s ease-out;
  }
  
  .nav-item:hover .Entry {
      display: block;
  }

  .nav-item:hover .Reports {
      display: block;
    }

    .nav-item:hover .New {
        display: block;
    }

    .nav-item:focus-within .Entry {
        display: block;
    }

    .nav-item:focus-within .Reports {
      display: block;
  }

  .nav-item:focus-within .New{
    display: block;
}
